import SlideTempList from "components/SlideTemp/SlideTempList";
import ProductModal from "components/Store/ProductModal";
import { useState } from "react";
import { useSelector } from "react-redux";
import { MoviePreview as Container, SectionTitle } from "./Movie.styled";
import MovieGallery from "./MovieGallery";

const MoviePreview = () => {
  const [LargerView, setLargerView] = useState("");
  const { isMobileDevice } = useSelector(({ _MainReducer }) => _MainReducer);
  const [largerMobileView, setLargerMobileView] = useState(-1);
  const handleLargerView = (e) => {
    const src = e.currentTarget.src;
    const index = e.currentTarget.getAttribute("dataIndex");
    if (isMobileDevice) {
      setLargerMobileView(index);
    } else {
      setLargerView(src);
    }
  };
  const handleCloseModal = () => {
    if (isMobileDevice) {
      setLargerMobileView(-1);
    } else {
      setLargerView("");
    }
  };
  const [gallery] = useState([
    `https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false`,
    `https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false`,
    `https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false`,
    `https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false`,
    `https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false`,
  ]);
  return (
    <Container>
      <SectionTitle>پیش نمایش</SectionTitle>

      {LargerView?.length > 0 ? (
        <ProductModal
          style={{ padding: "0px" }}
          h="auto"
          width="auto"
          mw="fit-content"
          blur={handleCloseModal}
          onClick={handleCloseModal}
        >
          <img src={LargerView} alt="" style={{ cursor: "pointer" }} />
        </ProductModal>
      ) : null}
      {largerMobileView > -1 ? (
        <MovieGallery
          gallery={gallery}
          handleHideGallery={handleCloseModal}
          index={largerMobileView}
        />
      ) : null}
      <SlideTempList>
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          className="BgError"
          onClick={handleLargerView}
          dataIndex={0}
        />
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          onClick={handleLargerView}
          className="BgError"
          dataIndex={1}
        />
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          className="BgError"
          dataIndex={2}
          onClick={handleLargerView}
        />
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          className="BgError"
          dataIndex={3}
          onClick={handleLargerView}
        />
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          className="BgError"
          dataIndex={4}
          onClick={handleLargerView}
        />
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          className="BgError"
          dataIndex={5}
          onClick={handleLargerView}
        />
        <img
          src="https://video-extras.cafebazaar.ir/filimo/e4339b3d-1b02-4a67-abff-95a7565a0500?x-img=v1/resize,h_800/format,type_webp,lossless_false"
          alt=""
          className="BgError"
          onClick={handleLargerView}
          dataIndex={6}
        />
      </SlideTempList>
    </Container>
  );
};

export default MoviePreview;
